
const tabsBtn = document.querySelectorAll('.card__tab-btn'),
      tabsItems = document.querySelectorAll('.card__tab-content');
tabsBtn.forEach( (el) => {
    el.addEventListener('click', () => {
        let tabId = el.getAttribute('data-tab');
        let currentTab = document.querySelector(tabId);

        if( ! el.parentElement.classList.contains('active')) {
            tabsBtn.forEach( (el) => {
                el.parentElement.classList.remove('active');
            });

            tabsItems.forEach( (el)=> {
                el.classList.remove('active');
            });
            el.parentElement.classList.add('active');
            currentTab.classList.add('active');
        }

    });
});
document.querySelector('.card__tab-btn').click();

const gallerry = document.querySelectorAll('.card__gallery-item');
gallerry.forEach( (item) => {
    item.addEventListener('click', ()=> {
        gallerry.forEach( (item)=> item.classList.remove('active'));
        item.classList.add('active');
        document.querySelector('.card__img img').src = item.querySelector('img').src
    });

});
document.querySelector('.card__gallery-item').click();

const backetAmount = document.querySelector('#backetAmount'),
      backetCountGood = document.querySelector('#backetCount'),
      btnAddBacket = document.querySelectorAll('.addToBacket');
let countGood = 0;
btnAddBacket.forEach( (item)=> {
    item.addEventListener('click', ()=> {
        countGood++;
        if (countGood == 1) {
            backetCountGood.textContent = `${countGood} товар`;
        } else if (countGood > 1 && countGood < 5) {
            backetCountGood.textContent = `${countGood} товара`;
        } else {
            backetCountGood.textContent = `${countGood} товаров`;
        }
    });
});


